<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/notice">消息列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24" v-loading="loading">
      <el-col>
        <el-form-item label="名称">
          <el-input v-model="form.name" placeholder="请输入名称"></el-input>
        </el-form-item>
      </el-col>
      <el-col v-if="form.type === 'user'">
        <el-form-item label="用户手机号">
          <el-input v-model="form.mobile" placeholder="请输入内容"></el-input>
        </el-form-item>
      </el-col>
      <el-col v-if="form.type === 'vip'">
        <el-form-item label="vip等级">
          <el-select v-model="form.vip_id" clearable placeholder="请选择">
            <el-option v-for="(value, key) in config.vip" :key="key" :label="value.name" :value="value.id"> </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="内容" style="display: flex;">
          <vue-editor
            useCustomImageHandler
            @image-added="handleImageAdded"
            v-model="form.content"
            style="flex: 1;"
          ></vue-editor>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import { Message } from 'element-ui'
import { VueEditor } from 'vue2-editor'

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      loading: false,
      form: {
        title: '',
        content: '',
        type: 'none',
      },
      config: [],
    }
  },
  computed: {},
  mounted() {
    this.loading = true
    const id = this.$router.history.current.params.id
    if (id) {
      Fetch2('/user/notice/edit/' + id)
        .then(r => {
          this.form = r.data.view
          this.config = r.data.config
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    } else {
      Fetch('/user/notice/config')
        .then(r => {
          this.config = r.data
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    }
  },
  methods: {
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      this.loading = true
      const id = this.$router.history.current.params.id
      Fetch('/user/notice/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      })
        .then(() => {
          this.loading = false
          Message.success('操作成功')
          this.$router.go(-1)
        })
        .catch(() => {
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
